
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

* { -webkit-box-sizing: border-box; box-sizing: border-box
}
ul {margin : 0 ; padding : 0
}
ul li {
    cursor: pointer ; position : relative;padding : 8px 8px 8px 40px;
    background:#eee;font-size:14px; -webkit-transition : 0.2s; transition : 0.2s;
    -webkit-user-select: none;-moz-user-select: none;
    -ms-user-select: none; user-select: none;
}
ul li:hover {background:#ddd
}
ul li.checked{background:#BBB;color:#fff;text-decoration: line-through;
}
.close{
    position: absolute ; right: 0 ; top: 0;
}
.close:hover{
    background-color: #f44336; color: white
}
.header{
    background-color: white ; padding: 30px 30px;
    color : black; text-align: center;
}
.header:after{
    content:"";display: table; clear:both;
}
.input{
    border:none ; width: 75%; height: 35px; padding:10px;
    float: left; font-size:16px
}
.addbutton{
    padding:10px; width:24% ; height: 35px ; background : #2D3E50;
    color:white;
    float : left; text-align: center;
    font-size: 13px; cursor: pointer; -webkit-transition: 0.3s; transition: 0.3s;
    font-weight: bold;
    font-family: 'Nanum Gothic';
    border-radius:10px;
    margin-left:1px;
}
.addbutton:hover{background-color:#bbb
}
.completed {text-decoration: none
}
.input{outline:none;border:1px solid;border-radius:10px
}
